import styled from "styled-components";

export const Wrapper = styled.div`
@media only screen and (max-width:768px){
    display: none
}
.Bottom{
    position: fixed;
    bottom:-10px;
    width: 300px;
    z-index: 1000;
    transition: 1s;
    left: 0;
}
.Bottom-top{
    display: flex;
    justify-content: space-between;
    background-color: #68bb46;
    padding: 20px;
    border-top-right-radius: 50px;
    cursor: pointer;
}
.Bottom-top-Close{
    display: none;
    justify-content: space-between;
    background-color: #68bb46;
    padding: 20px;
    border-top-right-radius: 50px;
    cursor: pointer;
}

.Bottom-top-Close span{
    color: white;
    font-size: 24px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.Bottom-top span{
    color: white;
    font-size: 24px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.item{
    margin: 0;
    padding: 0;
    background-color: white;
    list-style-type: none;
    width: 95.7%;
    padding: 5px;
    border-right: 2px solid #68bb46;
  }
  .list{
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-bottom: 10px;
    margin-top: 10px;
  }
  #title{
    color: #68bb46;
    font-size: 14px;
    font-weight: 500;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;  
  }
    .menu-catalog{
        display: none;
    }
    .media-Footer{
        display: none;
    }
`