import { useRef } from "react";
import { useEffect, useState } from "react"
import { Wrapper , Abouts  , WrapperAboutError , AboutMobileLang} from "./styled-index"
import DescriptionErrorImage from "./../../assets/Design stats-pana 1.png"
import DescriptionErrorImage2 from "./../../assets/Business analytics-amico 1.svg"

function Model3dComponent({DescriptionError}) {
    const [Model , setModels] = useState([]);
    const [About , setAbout] = useState([]);
    const [lang , setLang] = useState("uz")
    const id = window.localStorage.getItem("ModelId")
    window.localStorage.setItem("Lang" , lang)
    useEffect(() => {
        fetch(`https://artel-back-end.herokuapp.com/models/${id || 1}`)
        .then(res => res.json())
        .then(result => setModels(result))
    }, [Model])
    useEffect(() => {
        fetch(`https://artel-back-end.herokuapp.com/models`)
        .then(res => res.json())
        .then(result => setAbout(result))
    }, [About])
    return(
        <Wrapper>
            {window.localStorage.getItem("DescriptionError") === "true" ? Model.Models?.map(elem => Number(window.localStorage.getItem("ModelNakleka"))=== elem.id ? (
                    <div class="Sirv" data-src={elem.modelKadr}></div>
            ):null) : null}
{!window.localStorage.getItem("DescriptionError") ? 
            <WrapperAboutError>
                <img src={DescriptionErrorImage} width={600} height={400} alt="" />
                <h1>Model tanlang !</h1>
            </WrapperAboutError> : null}
            {window.localStorage.getItem("DescriptionError") === "false" ?
                <WrapperAboutError>
                <img src={DescriptionErrorImage2} width={550} height={450} alt="" />
                <h1>Dizayn tanlang !</h1>
            </WrapperAboutError> : null}
            <Abouts>
                {window.localStorage.getItem("DescriptionError") === "true" ? About.map(elem => Number(window.localStorage.getItem("ModelId")) === elem.id ? (
                    <>
                        <h5>{elem.modelTitle}</h5>
                        {window.localStorage.getItem("Lang") === "uz" ? <h6>{elem.modelDescriptionTitleUz}</h6> : window.localStorage.getItem("Lang") === "ru" ? <h6>{elem.modelDescriptionTitleRu}</h6>:window.localStorage.getItem("Lang") === "en" ? <h6>{elem.modelDescriptionTitleEn}</h6>:null}
                        {window.localStorage.getItem("Lang") === "uz" ?<img src={elem.modelDescriptionUz} width={680} height={450} alt="" />  : window.localStorage.getItem("Lang") === "ru" ?<img src={elem.modelDescriptionRu} width={680} height={450} alt="" /> :window.localStorage.getItem("Lang") === "en" ? <img src={elem.modelDescriptionEn} width={680} height={450} alt="" />:null}
                        <AboutMobileLang>
                            <span onClick={() => setLang("uz")}>UZ</span>
                            <hr />
                            <span onClick={() => setLang("ru")}>RU</span>
                            <hr />
                            <span onClick={() => setLang("en")}>EN</span>
                        </AboutMobileLang>
                    </>
                ) :null): null}
            </Abouts>
        </Wrapper>
    )
}
export default Model3dComponent