import { useState } from "react"
import { useRef } from "react"
import { useEffect } from "react"
import Hamburger  from "./../../assets/icons-hamburgerf.png"
import ModelComponent from "./models"
import { Wrapper } from "./styled-index"
function ModelsComponent({setDescription}) {
    const [ModelsTitle , setModelsTitle] = useState([])
    const BottomModels = useRef();
    const [open , setOpen ] = useState(true);
    useEffect(() => {
        fetch("https://artel-back-end.herokuapp.com/models")
        .then(res => res.json())
        .then(result => setModelsTitle(result))
    }, [ModelsTitle])
    const HandleOpen = () =>{
        BottomModels.current.style.bottom = "-230px" 
        setOpen(false)
    }
    const HandleClose = () =>{
        BottomModels.current.style.bottom = "-10px" 
        setOpen(true)
    }

    return(
        <Wrapper>
           <div ref={BottomModels} class="Bottom" >
        <div class="Bottom-top">
        
        {open === true ?  <span id="Open-text">Modellarni yopish</span> : null}   
        {open === false ? <span id="Close-text">Modellarni ko'rish</span> : null}
        {open === true ?  <img id="Open" onClick={HandleOpen} src={Hamburger} width="30" height="30" alt=""/>:null}
        {open === false ? <img id="Close" onClick={HandleClose} src={Hamburger} width="30" height="30" alt=""/>:null}
        </div>
        <ol class="item">
            {ModelsTitle.map(elem =>
                <ModelComponent setDescription={setDescription} elem={elem}/>
            )}
        </ol>
    </div> 
        </Wrapper>
    )
}
export default ModelsComponent