import styled from "styled-components";

export const Wrapper = styled.div`
    .Right{
    position: absolute;
    top: 0;
    right: 0px;
}
.Colors{
    display: flex;
    flex-direction: column;
    height: 50vh;
    margin-top: 150px;
    justify-content: space-around;
    align-items: flex-end;
    margin-right: 20px;
}
.Colors button{
    width: 50px;
    height: 50px;
    border-radius: 50px;
    background-color: transparent;
    cursor: pointer;
}
.Colors button:nth-child(1)::after{
    content: "";
    display: block;
    position: relative;
    background-color: transparent;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background-color: #68bb46;
}
.Colors button:nth-child(1){
    border:5px solid  #68bb46;
}
.Colors button:nth-child(4){
    border:5px solid  #68bb46;
}
.Colors button:nth-child(2)::after{
    content: "";
    display: block;
    position: relative;
    background-color: transparent;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background-color: rgb(150, 26, 26);
}
.Colors button:nth-child(2){
    border:5px solid  rgb(150, 26, 26);
}
.Colors button:nth-child(3)::after{
    content: "";
    display: block;
    position: relative;
    background-color: transparent;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background-color: rgb(25, 162, 216);
}
.Colors button:nth-child(3){
    border:5px solid  rgb(25, 162, 216);
}
@media only screen and (max-width:768px){
    display: none
}
`