import Model3dComponent from "../../component/model-3d";
import ColorButton from "../../component/model-colors";
import ModelNakleka from "../../component/model-nakleka";
import { Wrapper , WrapperRight } from "./styled-model";
import ModelsComponent from "../../component/model-component";
import ModelAbout from "../../component/model-about";
import MediaFooter from "../../component/media-footer";
import { useState } from "react";
function ModelArtel() {
    const [DescriptionError , setDescription] = useState(true);
    return(
        <>
        <Wrapper>
        <ModelNakleka/>
            <div className="ModelComponent">
            <Model3dComponent DescriptionError={DescriptionError}/>
        <ModelAbout/>
            </div>
        <ColorButton/>
        <ModelsComponent setDescription={setDescription}/>
        </Wrapper>
        <MediaFooter/>
        </>

    )
}
export default ModelArtel;