import styled from "styled-components";
import UzFlag from "./../../assets/uzbekistan-flag-icon.svg"
import RuFlag from "./../../assets/russian-flag.png"
import EnFlag from "./../../assets/england-flag.png"

export const Wrapper = styled.div`
    display: block;
    width: ${({listenDisplay}) => listenDisplay ? "50%": "0%"};
    margin-top: 130px;
    border:${({listenDisplay}) => listenDisplay ? " 5px solid #68bb46 ": " 0px solid transparent"};
    padding: 15px;
    border-radius: 20px;
    margin-bottom: 140px;
    margin-left: 20px;
    transition: 1s;
    h5{
    font-size: ${({listenDisplay}) => listenDisplay ? "24px": "0px"};
    font-family: 'Gill Sans','Gill Sans MT',Calibri,'Trebuchet MS',sans-serif;
    margin: 0;
    padding: 0;
    margin-bottom: 10px;
    transition: 1.2s;
    }
    h6{
    font-size: ${({listenDisplay}) => listenDisplay ? "18px": "0px"};
    font-family: 'Gill Sans','Gill Sans MT',Calibri,'Trebuchet MS',sans-serif;  
    margin: 0;
    padding: 0;
    transition: 1.3s;
    }
    img{
        width: ${({listenDisplay}) => listenDisplay ? "680px": "0px"};
        height: ${({listenDisplay}) => listenDisplay ? "450px": "0px"};
    margin: 0;
    padding: 0;
    transition: 1.3s;
    }
    p{
    font-size: ${({listenDisplay}) => listenDisplay ? "16px": "0px"};
    line-height: 20px;
    font-family: 'Gill Sans','Gill Sans MT',Calibri,'Trebuchet MS',sans-serif;  
    margin: 0;
    padding: 0;
    transition: 1.5s;
    }
    @media only screen and (max-width:768px){
        width: 100%;
        display: none;
}
`
export const AboutTop = styled.div`
    display: flex;
    justify-content: space-between;
`
export const AboutLang = styled.div`
    display: flex;
    justify-content: space-between;
    width: 34%;
    span:nth-child(1)::before{
content: "";
display: block;
position: relative;
right: 5px;
top: 3px;
background-image: url(${UzFlag});
width:${({listenDisplay}) => listenDisplay ? "30px": "0px"};
height:${({listenDisplay}) => listenDisplay ? "15px": "0px"};
background-repeat: no-repeat;
background-size: cover;
transition: 1.5s;
}
span:nth-child(3)::before{
content: "";
display: block;
position: relative;
right: 5px;
bottom: 4px;
background-image: url(${RuFlag});
width: ${({listenDisplay}) => listenDisplay ? "30px": "0px"};
height: ${({listenDisplay}) => listenDisplay ? "23px": "0px"};
background-repeat: no-repeat;
background-size: cover;
transition: 1.5s;

}
span:nth-child(5)::before{
content: "";
display: block;
position: relative;
right: 5px;
bottom: 4px;
background-image: url(${EnFlag});
width: ${({listenDisplay}) => listenDisplay ? "30px": "0px"};
height: ${({listenDisplay}) => listenDisplay ? "25px": "0px"};
background-repeat: no-repeat;
background-size: cover;
transition: 1.5s;
}
    span{
        padding: ${({listenDisplay}) => listenDisplay ? "0px": "10px"};
        font-family: 'Gill Sans','Gill Sans MT',Calibri,'Trebuchet MS',sans-serif;
        cursor: pointer;
        font-size: ${({listenDisplay}) => listenDisplay ? "18px" : "0px"};
        transition: 1.6s;
        display: flex;
    }
    hr{
        background-color: ${({listenDisplay}) => listenDisplay ? "gray" : "white"};
        height: ${({listenDisplay}) => listenDisplay ? "15px" : "0px"};
        margin-top: ${({listenDisplay}) => listenDisplay ? "2px" : "0px"};
        margin-left: 5px;
        margin-right: 5px;
        border: 1px solid transparent;
        transition: 1.6s;
    }
`