import { Wrapper } from "./styled-index"
import Open from "./../../assets/hamburger-gray.png"
import { useRef } from "react"
import ModelNaklekaMobile from "./model-naklekaMobile";
import ModelMobileComponent from "./media-catalog/modelsMobile"
import CatalogClose from "../../assets/close-icon-catalog.png"
import { useState } from "react"
import { useEffect } from "react"
function MediaFooter() {
    const MediaFooter = useRef();
    const Overlay = useRef();
    const Close = useRef();
    const OpenRef = useRef();
    const MenuCatalog = useRef();
    const HandleOpen = () =>{
        MediaFooter.current.style.height = "75vh"
        Overlay.current.style.display = "block"
        OpenRef.current.style.display = "none"
        Close.current.style.display = "block"

    }
    const HandleClose = () =>{
        MediaFooter.current.style.height = "10vh"
        Overlay.current.style.display = "none"
        OpenRef.current.style.display = "block"
        Close.current.style.display = "none"
    }
    const [ModelsTitle , setModelsTitle] = useState([])
    useEffect(() => {
        fetch("https://artel-back-end.herokuapp.com/models")
        .then(res => res.json())
        .then(result => setModelsTitle(result))
    }, [ModelsTitle])
    return(
        <Wrapper>
    <div ref={MediaFooter} class="media-Footer">
        <img  ref={OpenRef} class="media-open" onClick={HandleOpen} src={Open} width="50" height="35" alt=""/>
        <img  ref={Close} class="media-close" onClick={HandleClose} src={Open} width="50" height="35" alt=""/>
        <div class="Colors-about">
            <span>Colors</span>
        <div class="Colors-media">
            <button></button>
            <button></button>
            <button></button>
        </div>
    </div>
        <div class="Nakleka-about">
            <span>Dizayn</span>
            <div class="Nakleka-media">
            <ModelNaklekaMobile MediaFooter={MediaFooter} Overlay={Overlay}/>
            </div>
        </div>
         <button class="Model-watch"  onClick={()=>{
            MenuCatalog.current.style.left = "0px"
            MenuCatalog.current.style.transition = "1.5s"
         }}>Modellarni ko'rish</button>
        </div>
    <div class="overlay" onClick={()=>{
                MediaFooter.current.style.height = "10vh"
                Overlay.current.style.display = "none"
                OpenRef.current.style.display = "block"
                Close.current.style.display = "none"
    }} ref={Overlay}>

    </div>
    <div  ref={MenuCatalog} class="menu-catalog">
    <div class="menu-catalog-top">
        <img class="menu-catalog-close" onClick={()=>{
            MenuCatalog.current.style.left = "900px"
         }} src={CatalogClose} width="40" height="40" alt=""/>
        <span>Modellarni ko'rish</span>
    </div>
    <ul class="item-media">
    {ModelsTitle.map(elem =>
                <ModelMobileComponent elem={elem}/>
            )}
    </ul>
</div> 
        </Wrapper>
    )
}
export default MediaFooter