import { Wrapper } from "./styled-index"
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import { useState } from "react";


function ColorButton() {
    const [HandleCloseOpen , setHandleCloseOpen] = useState(true);
    const HandleClick = () =>{
        window.localStorage.setItem("About" , "true")
        setHandleCloseOpen(true)
    }
    const HandleClose = () =>{
        window.localStorage.setItem("About" , "false")
        setHandleCloseOpen(false)
    }
    return(
        <Wrapper>
    <div class="Right">
        <div class="Colors">
            <button></button>
            <button></button>
            <button></button>
            {HandleCloseOpen === false ? (
                    <Tooltip title="Modelni ma'lumotni ko'rish" onClick={HandleClick} placement="left">
                    <IconButton>
                <box-icon name='info-circle' color='#68bb46' ></box-icon>
              </IconButton>
                  </Tooltip>
            ): HandleCloseOpen === true ?(
                <Tooltip title="Modelni ma'lumotni yopish" onClick={HandleClose} placement="left">
                <IconButton>
            <box-icon name='info-circle' color='#68bb46' ></box-icon>
          </IconButton>
              </Tooltip>
            ):null}
        </div>
    </div>
        </Wrapper>
    )
}
export default ColorButton