import styled from "styled-components";
import UzFlag from "./../../assets/uzbekistan-flag-icon.svg"
import RuFlag from "./../../assets/russian-flag.png"
import EnFlag from "./../../assets/england-flag.png"
import Fullscreen from "./../../assets/icon-zoom.png"
export const Wrapper = styled.div`
    width: 60%;
    height: 60vh;
    margin: 0 auto;
    z-index: 1;
    transition: 1s;
    .v360-viewport{
        background-color: transparent !important;
    }
    .Sirv .sirv-spin-container .sirv-spin-button{
        background-image: url(${Fullscreen});
        background-size: cover;
        width: 48px;
        height: 48px;
        z-index: 1000000;
    }
    .spin-figure > canvas{
        cursor: grab !important;
    }
    .sirv-spin-button.fullscreen{
        background-position: 0px 0 !important;
    }
    margin-top: 100px;
    @media only screen and (max-width:768px){
        width: 100%;
        height: 65vh; 
        overflow: scroll;
}

    @media only screen and (max-width:426px){
        width: 100%;
        height: 65vh; 
        .spin-figure canvas{
            width: 560px;
            height: 450px;
        }  
}
`
export const AboutMobileLang = styled.div`
display: flex;
width: 70%;
align-items: center;
margin: 0 auto;
span{
display: flex;
margin-right: 5px;
font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
span:nth-child(1)::before{
content: "";
display: block;
position: relative;
right: 5px;
top: 3px;
background-image: url(${UzFlag});
width: 30px;
height: 15px;
background-repeat: no-repeat;
background-size: cover;
}
span:nth-child(3)::before{
content: "";
display: block;
position: relative;
right: 5px;
bottom: 4px;
background-image: url(${RuFlag});
width: 30px;
height: 23px;
background-repeat: no-repeat;
background-size: cover;
}
span:nth-child(5)::before{
content: "";
display: block;
position: relative;
right: 5px;
bottom: 4px;
background-image: url(${EnFlag});
width: 30px;
height: 25px;
background-repeat: no-repeat;
background-size: cover;
}
hr{
    height: 20px;
    background-color: gray;
}
`
export const Abouts = styled.div`
    display: none;
    
    @media only screen and (max-width: 768px) {
        display: block;
        margin: 0;
        padding: 0;
        margin-top: -80px;
        h5{
            margin: 0 ;
            padding: 0;
            font-size: 24px;
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        }
        img{
            width: 110%;
            height: 55vh;
        }
        h6{
            margin: 0 ;
            padding: 0;
            margin-top: 10px;
            font-size: 18px;
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        }
    }
`
export const WrapperAboutError = styled.div`
        text-align: right;
    h1{
        color: #68bb46;
        font-size: 64px;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    }
    @media only screen and (max-width:768px) {
        text-align: center;
        img{
            width: 90%;
        }
        h1{
        color: #68bb46;
        font-size: 34px;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    }
    }
    @media only screen and (max-width:600px) {
        text-align: center;
        img{
            width: 90%;
            height: 250px;
        }
        h1{
        color: #68bb46;
        font-size: 34px;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        margin-right: 0px;
    }
    }
`