import styled from "styled-components";

export const Wrapper = styled.div`
    width: 100%;
    position: fixed;
    display: flex;
    justify-content: space-between;
    top: 0;
.Logo{
    margin-top: 10px;
    margin-left: 10px;
}
.right{
    background-color: white;
    border-bottom: 5px solid #68bb46;
    border-left: 5px solid #68bb46;
    box-shadow: 0px 0px 5px 4px rgb(235, 234, 234);
    width: 500px;
    display: flex;
    justify-content: space-around;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom-left-radius: 50px;
}
h4{
    margin: 0;
    padding: 0;
    color: #68bb46;
    font-size: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
@media only screen and (max-width:768px){
   .right{
    display: none
   }
}
`