import { useState } from "react"
import { useRef } from "react";
import { useEffect } from "react"
import { Wrapper ,AboutTop , AboutLang } from "./styled-index"



function ModelAbout() {
    const [About , setAbout ] = useState([]);
    const [lang , setLang] = useState("uz")
    useEffect(() => {
        fetch("https://artel-back-end.herokuapp.com/models")
        .then(res => res.json())
        .then(result => setAbout(result))
    }, [About])
    window.localStorage.setItem("Lang" , lang)

    return(
        <Wrapper listenDisplay={window.localStorage.getItem("About") === "true"}>
                        {About.map(elem => Number(window.localStorage.getItem("ModelId"))=== elem.id ? (
                        <>
                        <AboutTop>
                        <h5>{elem.modelTitle}</h5>
                        <AboutLang listenDisplay={window.localStorage.getItem("About") === "true"}>
                            <span listenDisplay={window.localStorage.getItem("About") === "true"} onClick={() => setLang("uz")}>UZ</span>
                            <hr />
                            <span listenDisplay={window.localStorage.getItem("About") === "true"} onClick={() => setLang("ru")}>RU</span>
                            <hr />
                            <span listenDisplay={window.localStorage.getItem("About") === "true"} onClick={() => setLang("en")}>EN</span>
                        </AboutLang>
                        </AboutTop>
                        {window.localStorage.getItem("Lang") === "uz" ? <h6>{elem.modelDescriptionTitleUz}</h6> : window.localStorage.getItem("Lang") === "ru" ? <h6>{elem.modelDescriptionTitleRu}</h6>:window.localStorage.getItem("Lang") === "en" ? <h6>{elem.modelDescriptionTitleEn}</h6>:null}
                        {window.localStorage.getItem("Lang") === "uz" ?<img src={elem.modelDescriptionUz} width={680} height={450} alt="" /> : window.localStorage.getItem("Lang") === "ru" ?<img src={elem.modelDescriptionRu} width={680} height={450} alt="" /> :window.localStorage.getItem("Lang") === "en" ? <img src={elem.modelDescriptionEn} width={680} height={450} alt="" />:null}
                        </>
            ):null)}

        </Wrapper>
    )
}
export default ModelAbout