import { Wrapper } from "./index-styled"
import NaklekaImage from "./../../img/nakleka.png"
import Logo from "./../../assets/logo.png"
import { useState } from "react";
import { useEffect } from "react";
function ModelNakleka() {
    const [Nakleka , setNaklekas] = useState([]);
    const id = window.localStorage.getItem("ModelId")
    useEffect(() => {
        fetch(`https://artel-back-end.herokuapp.com/models/${id}`)
        .then(res => res.json())
        .then(result => setNaklekas(result))
    }, [])
    return(
        <Wrapper>
            <img src={Logo} className="Logo" width={66} height={26} alt="logo" />
            <div className="right">
            {Nakleka.Models?.map(elem =>(
            <img src={elem.modelNaklekaImage} style={{cursor:"pointer"}} id={elem.id} onClick={(e) => {
                window.localStorage.setItem("ModelNakleka" , e.target.id)
                window.localStorage.setItem("DescriptionError" , true)
            }} width="120" height="50" alt=""/>
            ))}

            {!window.localStorage.getItem("DescriptionError") ? <div className="DescriptionError">
                <h4>Dizayn yo'q</h4>
            </div> : null}
            </div>
        </Wrapper>
    )
}
export default ModelNakleka