import { useRef } from "react";
import { List } from "./styled-index";

function ModelMobileComponent({elem}) {
    const IdRef = useRef();

    const HandleId =()=>{
        window.localStorage.setItem("ModelId" , IdRef.current.id)
        window.location.reload();
        window.localStorage.setItem("DescriptionError" , false)

    }
    return(
        <>
                <List onClick={HandleId}>
                    <div id={elem.id} className="list" ref={IdRef} >
                    <img src={elem.modelImage} width={80} height={60}  alt="" />
                <span id="title">{elem.modelTitle}</span>
                    </div>
                    <hr /> 
                </List>
        </>
    )
}
export default ModelMobileComponent